header {
    height: 100%;
    padding-top: 7rem;
    overflow: hidden;
}
.header__container {
    text-align: center;
    height: 100;
    position: relative;
}
.milan {
    transform: rotate(345deg);
}
.animation {
    color: var(--color-primary);
    padding-top: 400px;
    display: flex;
    justify-content: center;
}

/* ===================== CTA ====================== */

.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/* ===================== HEADER SOCIALS ====================== */

.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
}
.header__socials::after {
    content: "";
    width: 1px;
    height: 4rem;
    background: var(--color-primary);
}

/* ===================== ME ====================== */

.me {
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 18rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 2rem 1.5rem 1.5rem 1.5rem;
}

/* ===================== SCROLL DOWN ====================== */

.scroll__down {
    position: absolute;
    right: -2.3rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/* ======================== MEDIA QUERIES (MEDIUM DEVICES) ========================= */

@media screen and (max-width: 1024px) {
    header {
        height: 100%;

    }
}

/* ======================== MEDIA QUERIES (SMALL DEVICES) ========================= */

@media screen and (max-width: 600px) {

    .header__socials, .scroll__down {
        display: none;
    }
}
